      userLogin: '',
      // 存储手机号码
      phone: ''
    };
  },
  onLoad() {
    this.userLogin = uni.getStorageSync('userLogin')
    this.phone = uni.getStorageSync('phone')
    if (this.userLogin == '') {
      this.isLogin = true
    } else {
      this.isLogin = false
    }
  },
  onShow() {
    this.userLogin = uni.getStorageSync('userLogin')
    this.phone = uni.getStorageSync('phone')
    if (this.userLogin == '') {
      this.isLogin = true
    } else {
      this.isLogin = false
    }
  },
  methods: {
    // 去会员卡页面
    goVipCard() {
      uni.navigateTo({
        url: "/pages/vipCard/vipCard"
      });
    },
    // 去我的钱包页面
    goWallet() {
      uni.navigateTo({
        url: "/pages/wallet/wallet"
      });
    },
    // 去用户信息页面
    goMineUserInformation() {
      uni.navigateTo({
