      // 去我的订单页面
      if (index === 0) {
        return uni.navigateTo({
          url: "/pages/myOrder/myOrder"
        });
        // 去我的邀请码
      } else if (index === 2) {
				uni.request({
					url: "http://192.168.1.143:8086/WNC/user/getCode",
					data: {
						userId: 1,
					},
					header: {
						"Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
					},
					success: res => {
						console.log(res)
					}
        });
        return uni.navigateTo({
          url: "/pages/mineRecommendCourtesy/mineRecommendCourtesy"
        });
        // 去设置页面
      } else if (index === 7) {
        return uni.navigateTo({
          url: "/pages/mineSetUp/mineSetUp"
        });
        // 去地址管理页面
      } else if (index === 3) {
				uni.request({
					url: "http://192.168.1.143:8086/WNC/user/getAllAddress",
					data: {
						userId: 1,
					},
					header: {
						"Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
					},
					success: res => {
						console.log(res)
					}
        });
        return uni.navigateTo({
          url: "/pages/greenAddrManage/greenAddrManage"
        });
      } else if (index === 1) {
        // 去正在开发页面
				uni.navigateTo({
					url: '/pages/expect/expect'
				})
      }
      // 去售后服务页面
      // else if (index === 2) {
      //   return uni.navigateTo({
      //     url: "/pages/mineAfterSaleService/mineAfterSaleService"
      //   });
      // }
      // 去常见问题页面
      // else if (index === 7) {
      //   return uni.navigateTo({
      //     url: "/pages/mineCommonProblem/mineCommonProblem"
      //   });
      // } 
      // 去关于我们页面
      else if (index === 4) {
        uni.request({
					url: "http://192.168.1.143:8086/WNC/user/getUserAbout",
					data: {},
					header: {
						"Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
					},
					success: res => {
						console.log(res)
					}
        });
        // return uni.navigateTo({
        //   url: "/pages/mineAboutUs/mineAboutUs"
        // });
      }
      // 去意见反馈页面
      else if (index === 6) {
        return uni.navigateTo({
          url: "/pages/mineFeedback/mineFeedback"
        });
      }
    },
    // 获取用户信息
    // getUserInfos() {
    //   uni.getUserInfo({
    //     success: res => {
    //       console.log(res)
    //       this.defaultHead = res.userInfo.avatarUrl
    //       this.defaultUname = res.userInfo.nickName
    //       this.isLogin = false
    //     }
    //   })
    // }
    userInfo() {
      if (this.userLogin == '') {
        uni.navigateTo({
          url: "/pages/loginRegister/loginRegister"
        });
      } else {
        this.isLogin = true
      }
    }
  }
};
